/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  HostBinding,
  Injector,
  Input,
  IterableDiffers,
  NgZone,
  OnInit,
  Optional,
  Renderer2,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import {Router} from '@angular/router';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {animate, state, style, transition, trigger, useAnimation} from '@angular/animations';
import {EventManager} from '@angular/platform-browser';
import {Directionality} from '@angular/cdk/bidi';
import {
  AdaptCustomAction,
  AdaptDeviceDetectionService,
  AdaptFocusMonitorService, AdaptRadarAngularCustomEventName,
  AdaptRxControlLabelTooltip,
  AdaptTranslateService,
  ANIMATION_DURATION,
  ANIMATION_NAME,
  ANIMATION_TIMING_FUNCTION,
  LayerService,
  ResizeService,
  ScrollManageService
} from '@bmc-ux/adapt-angular';
import {
  AdaptSelectComponent,
  AdaptSelectSearchFilter,
  SelectModelFormat,
  SelectOption,
  SelectSettings
} from '../select/index';
import {AdaptRadarService} from '@bmc-ux/adapt-radar';

/**
 * VALUE_ACCESSOR for the select inline
 */
const SELECT_INLINE_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => AdaptSelectInlineComponent),
  multi: true
};

/** In a list menu 6 rows by default only visible - also it might be redefine on he devs end */
export const MENU_MAX_HEIGHT = 234;

@Component({
  selector: 'adapt-select-inline, div[select-inline]',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.a-select-inline]': 'true',
    '[class.is-error]': 'isErrorExist'
  },
  templateUrl: './select-inline.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [
    SELECT_INLINE_VALUE_ACCESSOR,
    AdaptSelectSearchFilter,
    AdaptFocusMonitorService
  ],
  animations: [
    trigger('validationFeedbackAnimation', [
      transition(':enter', [
        useAnimation(ANIMATION_NAME.fadeIn)
      ]),
      transition(':leave', [
        useAnimation(ANIMATION_NAME.fadeOut)
      ])
    ]),
    trigger('validationFeedbackChangeAnimation', [
      transition('* => *', [
        useAnimation(ANIMATION_NAME.opacity)
      ])
    ]),
    trigger('searchAnim', [
      transition('void => *', [
        style({visibility: 'hidden', opacity: 0}),
        animate(`.25s ${ANIMATION_TIMING_FUNCTION.base.transitionIn}`,
          style({visibility: 'visible', opacity: .6}))
      ]),
      transition('* => void', [
        style({visibility: 'visible', opacity: .6}),
        animate(`.25s ${ANIMATION_TIMING_FUNCTION.base.transitionInOut}`,
          style({visibility: 'hidden', opacity: 0}))
      ])
    ]),
    trigger('shadowAnimation', [
      transition('void => *', [
        style({visibility: 'hidden', opacity: 0, height: 0}),
        animate(`.25s ${ANIMATION_TIMING_FUNCTION.base.transitionIn}`,
          style({visibility: 'visible', opacity: 1, height: '*'}))
      ]),
      transition('* => void', [
        style({visibility: 'visible', opacity: 1, height: '*'}),
        animate(`.25s ${ANIMATION_TIMING_FUNCTION.base.transitionInOut}`,
          style({visibility: 'hidden', opacity: 0, height: 0}))
      ])
    ]),
    trigger('openClose', [
      state('show', style({
        width: '*',
        marginLeft: '0',
        marginRight: '.875rem',
        opacity: 1
      })),
      state('hide', style({
        marginRight: '0',
        width: '0px',
        opacity: 0
      })),
      transition('show => hide', [
        animate(`${ANIMATION_DURATION.medium} ${ANIMATION_TIMING_FUNCTION.base.transitionIn}`)
      ]),
      transition('hide => show', [
        animate(`${ANIMATION_DURATION.medium} ${ANIMATION_TIMING_FUNCTION.base.transitionInOut}`)
      ]),
      state('showRTL', style({
        width: '*',
        opacity: 1,
        marginLeft: '.875rem',
      })),
      state('hideRTL', style({
        marginLeft: '0',
        width: '0px',
        opacity: 0
      })),
      transition('showRTL => hideRTL', [
        animate(`${ANIMATION_DURATION.medium} ${ANIMATION_TIMING_FUNCTION.base.transitionIn}`)
      ]),
      transition('hideRTL => showRTL', [
        animate(`${ANIMATION_DURATION.medium} ${ANIMATION_TIMING_FUNCTION.base.transitionInOut}`)
      ]),
    ]),
  ],
  styles: [`
    .a-select-inline__list .dropdown-header {
      padding: .875rem .875rem .1875rem;
    }

    .select-inline__select-buttons {
      padding: 0 14px;
    }

    .select-inline__counter {
      display: inline-flex;
      overflow: hidden;
      color: #959799;
      white-space: nowrap;
      opacity: 0;
      align-self: center;
    }
  `]
})

export class AdaptSelectInlineComponent extends AdaptSelectComponent implements OnInit {

  /**
   * Need to redefine the default setting due to new requirements
   */

  defaultSettings: SelectSettings = {
    pullRight: false,
    enableSearch: true,
    checkedStyle: 'checkbox',
    buttonClasses: '',
    selectionLimit: 0,
    closeOnSelect: false,
    autoUnselect: false,
    showCheckAll: true,
    showUncheckAll: true,
    showTooltip: false,
    dynamicTitleMaxItems: 1,
    maxHeight: ' ',
    showCheckedAll: false,
    hideLabel: false,
    cancelBtn: true,
    doneBtn: true,
    titleMobile: true,
    modelFormat: SelectModelFormat.number
  };

  /**
   * Label icon with a tooltip or popover
   */
  tooltipConfig: AdaptRxControlLabelTooltip = null;

  /* new error behave for the selection - by default false */
  @Input() isErrorExist: boolean = false;

  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('sub-label') subLabel: string;
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('menu-height') menuHeight: number = MENU_MAX_HEIGHT;

  @HostBinding('class.a-select-inline_simple-view')
  @Input() simpleView: boolean = false;

  @ViewChild('inlineList', {static: true}) inlineList: ElementRef;

  get customActions(): AdaptCustomAction[] {
    return [
      {
        name: this.searchFilterText.value ? this.texts.checkAllFilter : this.texts.checkAll,
        visible: !this.isHiddenSelectFilteredButton(),
        onClick: this.checkAll.bind(this)
      },
      {
        name: this.searchFilterText.value ? this.texts.uncheckAllFilter : this.texts.uncheckAll,
        visible: !this.isHiddenUncheckFilteredButton(),
        onClick: this.uncheckAll.bind(this)
      }
    ];
  }

  constructor(public filterSearch: AdaptSelectSearchFilter,
              protected _renderer: Renderer2,
              protected detection: AdaptDeviceDetectionService,
              protected element: ElementRef,
              protected _focusHandle: AdaptFocusMonitorService,
              protected _scrollService: ScrollManageService,
              protected _changeDetectorRef: ChangeDetectorRef,
              protected _ngZone: NgZone,
              _layerService: LayerService,
              @Optional() protected _router: Router,
              _differs: IterableDiffers,
              _eventManager: EventManager,
              dir: Directionality,
              _resizeService: ResizeService,
              _injector: Injector,
              ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarService) {
    super(filterSearch, _renderer, detection, element, _focusHandle, _scrollService, _changeDetectorRef, _ngZone, _layerService,
      _router, _differs, _eventManager, dir, _resizeService, _injector, ts, _adaptRadarService, AdaptRadarAngularCustomEventName.AdaptSelectInline);
    this.single = false;
  }


  ngOnInit(): void {
    super.ngOnInit();

    this.settings = {...this.defaultSettings, ...this.settings};
    this.texts = {...this.defaultTexts, ...this.texts};

    if (this.tooltipInputIcon && !this.popoverIcon) {
      this.tooltipConfig = {
        iconName: this.tooltipInputIcon,
        content: this.tooltipInputText,
        placement: 'top',
        popoverMode: false
      };
    } else if (this.popoverIcon) {
      this.tooltipConfig = {
        iconName: this.popoverIcon,
        content: this.popoverContent,
        placement: this.popoverPlacement,
        popoverMode: true,
        popoverTriggers: this.popoverTriggers
      };
    }
  }

  trackByForOptions(index: number, option: SelectOption): any | number {
    return option.id || index;
  }
}
